<template>
    <div class="page-container">
        <div class="siteMenu-container">
            <div class="siteMenu-list">
                <div class="siteMenu-list-header">
                    <div class="up-down">
                        <v-btn class="white-button" @click="upDownsiteMenu('up')" depressed>
                            <font-awesome-icon icon="fa-solid fa-angle-up"/>
                        </v-btn>
                        <v-btn class="white-button" @click="upDownsiteMenu('down')" depressed>
                            <font-awesome-icon icon="fa-solid fa-angle-down"/>
                        </v-btn>
                    </div>
                    <div class="add">
                        <v-btn class="white-button" @click="addsiteMenu()" depressed>
                            <font-awesome-icon icon="fa-solid fa-plus"/>
                            &nbsp; 메뉴 추가
                        </v-btn>
                    </div>
                </div>
                <div class="siteMenu-list-body">
                    <ul v-for="(item,index) in siteMenuList" class="main-siteMenu">
                        <li class="main-li">
                            <div :class="'main-data '+item.active">
                                <div class="icon-box">
                                    <div v-if="item.subList && item.subList.length>0"
                                         @click="choicesiteMenu('main',index)">
                                        <font-awesome-icon class="icon" icon="fa-solid fa-folder-open"/>
                                        <!--font-awesome-icon class="icon" v-else icon="fa-solid fa-folder"/ -->
                                    </div>
                                    <div v-else @click="choicesiteMenu('main',index)">
                                        <font-awesome-icon class="icon" icon="fa-solid fa-file-lines"/>
                                    </div>
                                </div>
                                <div class="gname" @click="choicesiteMenu('main',index)">
                                    {{ item.mname }}
                                    <font-awesome-icon v-if="item.muse=='yes'" icon="fa-solid fa-eye"/>
                                    <font-awesome-icon v-else icon="fa-solid fa-eye-slash"/>
                                </div>
                                <div class="btn" @click="addSubMenu(index)">
                                    <font-awesome-icon class="plus" icon="fa-solid fa-circle-plus"/>
                                </div>
                                <div class="btn" @click="deletesiteMenu(index)">
                                    <font-awesome-icon icon="fa-solid fa-circle-minus"/>
                                </div>
                            </div>

                            <ul v-for="(subItem,subIndex) in item.subList" class="sub-siteMenu">
                                <li class="sub-li">
                                    <div :class="'sub-data '+subItem.active">
                                        <div class="icon-box">
                                            <div v-if="subItem.subList && subItem.subList.length>0"
                                                 @click="choicesiteMenu('sub',index,subIndex)">
                                                <font-awesome-icon class="icon" v-if="subItem.open"
                                                                   icon="fa-solid fa-folder-open"/>
                                                <font-awesome-icon class="icon" v-if="!subItem.open"
                                                                   icon="fa-solid fa-folder"/>
                                            </div>
                                            <div v-else @click="choicesiteMenu('sub',index,subIndex)">
                                                <font-awesome-icon class="icon" icon="fa-solid fa-file-lines"/>
                                            </div>
                                        </div>
                                        <div class="gname" @click="choicesiteMenu('sub',index,subIndex)">
                                            {{ subItem.mname }}
                                            <font-awesome-icon v-if="subItem.muse=='yes'" icon="fa-solid fa-eye"/>
                                            <font-awesome-icon v-else icon="fa-solid fa-eye-slash"/>
                                        </div>
                                        <div class="btn" @click="deleteSubsiteMenu(index,subIndex)">
                                            <font-awesome-icon icon="fa-solid fa-circle-minus"/>
                                        </div>
                                    </div>
                                </li>
                            </ul>
                        </li>
                    </ul>
                </div>
            </div>
            <div class="siteMenu-regist">
                <div v-if="fCode">
                    <div class="siteMenu-title">{{ siteMenuRegistTitle }}</div>
                    <form novalidate @submit.prevent="validate">
                        <ul class="table-ul">
                            <li>
                                <div class="label-input">
                                    <div class="input-box">
                                        <v-text-field v-model="mname"
                                                      :error-messages="mnameErrors"
                                                      dense
                                                      label="메뉴명"
                                                      outlined
                                                      :readonly="mnameReadonly"
                                                      @input="$v.mname.$touch()"
                                                      @blur="$v.mname.$touch()"
                                                      placeholder="메뉴명을 입력해주세요"/>
                                    </div>
                                </div>
                            </li>
                            <li>
                                <div class="label">
                                    메뉴 링크 유형
                                </div>
                                <div class="label-input">
                                    <div class="radio-box">
                                        <v-radio-group
                                                v-model="linkType"
                                                dense
                                                row
                                        >
                                            <!--v-radio
                                                    label="커스트마이징"
                                                    value="customize"
                                            ></v-radio-->
                                            <v-radio
                                                    label="게시판"
                                                    value="board"
                                            ></v-radio>
                                            <v-radio
                                                    label="URL 연결"
                                                    value="url"
                                            ></v-radio>
                                            <v-radio
                                                    label="사용안함"
                                                    value="no"
                                            ></v-radio>
                                        </v-radio-group>
                                    </div>
                                </div>
                            </li>
                            <li v-if="linkType=='board'" style="height:auto;max-height:300px;">
                                <div class="label">
                                    게시판 선택
                                </div>
                                <div style="height:auto;max-height:300px;">
                                    <div class="label-input flex">
                                        <div class="select-box" style="width:180px;margin-right:10px;">
                                            <v-select v-model="boardId"
                                                      dense
                                                      outlined
                                                      :error-messages="boardIdErrors"
                                                      @input="$v.boardId.$touch()"
                                                      @blur="$v.boardId.$touch()"
                                                      label="게시판 선택"
                                                      :items="boardList"/>
                                        </div>
                                        <div class="select-box" style="width:180px;margin-right:10px;">
                                            <v-select v-model="boardType"
                                                      dense
                                                      outlined
                                                      label="게시판 유형선택"
                                                      :items="boardTypeList"/>
                                        </div>
                                    </div>
                                    <div v-if="boardType=='view'" style="display: flex;">
                                        <div class="input-box" style="width:300px;">
                                            <v-text-field v-model="articleSubject"
                                                          dense
                                                          outlined
                                                          placeholder="검색어을 입력해주세요"/>
                                        </div>
                                        <v-btn class="white-button" @click="sarchArticle()" depressed style="max-width:70px;min-height: 40px;margin-left:0;background:#000 !important;color:#fff;margin-right:20px;" type="button">검색</v-btn>
                                        <div class="select-box" style="width:350px;">
                                            <v-select v-model="articleId"
                                                      dense
                                                      outlined
                                                      label="게시글 선택"
                                                      :items="articleList"/>
                                        </div>
                                    </div>
                                </div>

                            </li>
                            <li v-else-if="linkType=='url'">
                                <div class="label">
                                    URL
                                </div>
                                <div class="label-input flex">
                                    <div class="select-box" style="width:180px;margin-right:10px;">
                                        <v-select v-model="urlWinType"
                                                  dense
                                                  outlined
                                                  label="새창사용여부 선택"
                                                  :items="urlWinTypeList"/>
                                    </div>
                                    <div class="input-box" style="width:420px;">
                                        <v-text-field v-model="urlLink"
                                                      dense
                                                      outlined
                                                      placeholder="URL을 입력해주세요 (http://, https://) 를 반드시 입력해주세요"/>
                                    </div>
                                </div>

                            </li>
                            <li v-else-if="linkType=='customize'">
                                <div class="label">
                                    커스트마이징 코드
                                </div>
                                <div class="label-input flex">
                                    <div class="select-box" style="width:180px;margin-right:10px;">
                                        <v-select v-model="customizeId"
                                                  dense
                                                  outlined
                                                  :error-messages="customizeIdErrors"
                                                  @input="$v.customizeId.$touch()"
                                                  @blur="$v.customizeId.$touch()"
                                                  label="커스트마이징 코드 선택"
                                                  :items="customizeIdList"/>
                                    </div>

                                </div>
                            </li>
                            <li>
                                <div class="label">
                                    메뉴 사용여부
                                </div>
                                <div class="label-input">
                                    <div class="radio-box">
                                        <v-radio-group
                                                v-model="muse"
                                                dense
                                                row
                                        >
                                            <v-radio
                                                    label="사용"
                                                    value="yes"
                                            ></v-radio>
                                            <v-radio
                                                    label="사용안함"
                                                    value="no"
                                            ></v-radio>
                                        </v-radio-group>
                                    </div>
                                </div>
                            </li>
                        </ul>
                        <div class="button-row">
                            <v-btn class="submit-button" type="submit">메뉴정보 {{ buttonTitle }}</v-btn>
                        </div>
                    </form>
                </div>
            </div>
        </div>


    </div>
</template>


<script>
    import apiService from '@/config/admin/apiService'
    import {
        API_ADMIN_UPDATE_SETTING_MENU,
        API_ADMIN_GET_SETTING_MENU,
        API_ADMIN_GET_BOARD_ARTICLE_LIST,

    } from '@/config/admin/urls'
    import {validationMixin} from 'vuelidate'
    import {required, numeric} from 'vuelidate/lib/validators'

    export default {
        mixins: [validationMixin],
        validations: {
            mname: {required},
            boardId: {required},
            customizeId: {required}
        },
        computed: {
            mnameErrors() {
                const errors = []
                if (!this.$v.mname.$dirty) return errors
                !this.$v.mname.required && errors.push('메뉴명을 입력하세요')
                return errors;
            },
            boardIdErrors() {
                const errors = []
                if (!this.$v.boardId.$dirty) return errors
                !this.$v.boardId.required && errors.push('게시판을 선택하세요')
                if (errors.length < 1) {
                    const data = this.boardList.filter(item => item.value == this.boardId)
                    this.mname = data[0].text;
                }
                if(this.oldBoardId && this.oldBoardId != this.boardId) {
                    this.boardType = 'list';
                    this.articleSubject = '';
                    this.articleList = [];
                    this.articleId = '';
                }
                return errors;
            },
            customizeIdErrors() {
                const errors = []
                if (!this.$v.customizeId.$dirty) return errors
                !this.$v.customizeId.required && errors.push('커스트마이징 코드를 선택하세요')
                return errors;
            },
        },
        watch: {
            linkType(val) {
                if (val == 'board') {
                    if (this.boardId) {
                        const data = this.boardList.filter(item => item.value == this.boardId)
                        this.mname = data[0].text;
                    }
                    this.mnameReadonly = true;
                } else this.mnameReadonly = false;
            }
        },
        data() {
            return {
                siteMenuList: [],
                menuList: [],
                siteMenuRegistTitle: '메뉴 상세설정',
                mname: '',
                muse: 'yes',
                buttonTitle: '저장',
                rootActive: 'active',
                linkType: 'board',
                code: '',
                customizeId: '',
                customizeIdList: [
                    {text: '최신버전 다운로드', value: 'dowonLatest'},
                    {text: '이전버전 다운로드', value: 'dowonPre'},
                    {text: '하모니카 소개', value: 'about'},
                    {text: 'slack 참여하기', value: 'slack'},
                    {text: 'GitHub 참여하기', value: 'git'},
                ],
                sequenceParams: {
                    type: '',
                    id: '',
                    index: 0,
                    subIndex: 0,
                },
                serverFlag: false,
                boardList: [],
                boardId: '',
                boardType: 'list',
                boardTypeList: [
                    {value: 'list', text: '게시글 목록'},
                    {value: 'view', text: '게시글 상세'},
                ],
                articleId: '',
                articleText: '',
                articleList: [],
                fCode: '',
                sCode: '',
                subIndex: '',
                insert: false,
                update: false,
                mnameReadonly: false,
                articleSubject: '',
                urlWinType:'now',
                urlWinTypeList:[
                    {text:'새창 사용',value:'new'},
                    {text:'현재창 사용',value:'now'}
                ],
                urlLink:'',
                oldBoardId:'',


            };
        },
        created() {
            this.getData();
        },
        methods: {
            getData() {
                if (this.serverFlag) return;
                this.serverFlag = true;
                apiService.post(API_ADMIN_GET_SETTING_MENU, {}, (res) => {
                    this.serverFlag = false;
                    this.siteMenuList = [];
                    if (res.setting && res.setting.menu) {
                        const menuList = JSON.parse(res.setting.menu);
                        this.menuList = menuList;
                        for (let item of menuList) {
                            item.active = '';
                            item.open = false;
                            if (item.subList) {
                                let subList = [];
                                for (let subItem of item.subList) {
                                    subItem.active = '';
                                    subList.push(subItem)
                                }
                                item.subList = subList;
                            }
                            this.siteMenuList.push(item);

                        }

                    }
                    this.boardList = [];
                    if (res.boardList) {
                        for (const item of res.boardList) {
                            this.boardList.push({text: item.bname, value: item.bid})

                        }
                    }
                })
            },
            boxToggle(code) {
                this.choicesiteMenu(code, 'toggle');
            },
            choicesiteMenu(cmd, index, subIndex) {

                for (let menuItem of this.siteMenuList) {
                    menuItem.active = '';
                    if (menuItem.subList) {
                        for (let subItem of menuItem.subList) {
                            subItem.active = '';
                        }
                    }
                }
                this.insert = false;
                this.update = true;
                let activeInfo = {};
                if (cmd == 'main') {
                    activeInfo = this.siteMenuList[index];
                    this.fCode = activeInfo.code;
                    this.sCode = '';
                    this.sequenceParams = {
                        type: 'main',
                        code: activeInfo.code,
                        index: index,
                        subIndex: 0,
                    }

                } else {
                    activeInfo = this.siteMenuList[index].subList[subIndex];
                    this.fCode = this.siteMenuList[index].code;
                    this.sCode = activeInfo.code;
                    this.sequenceParams = {
                        type: 'sub',
                        code: activeInfo.code,
                        index: index,
                        subIndex: subIndex,
                    }

                }
                activeInfo.active = 'active';
                this.code = activeInfo.code;
                this.mname = activeInfo.mname;
                this.linkType = activeInfo.linkType;
                this.oldBoardId = '';
                switch (this.linkType) {
                    case 'board':
                        this.oldBoardId = activeInfo.boardId;
                        this.boardId = activeInfo.boardId;
                        this.customizeId = '';
                        this.mnameReadonly = true;
                        this.boardType = (activeInfo.boardType)?activeInfo.boardType:'list';
                        if(this.boardType=='view') {
                            this.articleId = activeInfo.articleId
                            this.articleList = [];
                            this.articleList.push({text:activeInfo.articleText,value:activeInfo.articleId});

                        }
                        this.urlWinType = 'now';
                        this.urlLink = '';

                        break;
                    case 'url':
                        this.boardId = '';
                        this.customizeId = '';
                        this.mnameReadonly = false;
                        this.urlWinType = activeInfo.urlWinType;
                        this.urlLink = activeInfo.urlLink;
                        this.boardType = 'list';
                        this.articleId = '';
                        break;
                    case 'customize':
                        this.boardId = '';
                        this.customizeId = activeInfo.customizeId;
                        this.mnameReadonly = false;
                        break;
                    default:
                        this.boardId = '';
                        this.customizeId = '';
                        this.urlWinType = 'now';
                        this.urlLink = '';
                        this.boardType = 'list';
                        this.articleId = '';

                        this.mnameReadonly = false;
                        break;
                }
                this.muse = activeInfo.muse;
            },
            addsiteMenu() {
                for (let item of this.siteMenuList) {
                    item.active = '';
                    if (item.subList) {
                        for (let subItem of item.subList) {
                            subItem.active = '';
                        }
                    }
                }

                this.insert = true;
                this.update = false;
                const codeDate = new Date();
                const code = 'm' + codeDate.getTime();
                this.fCode = code;
                this.subIndex = '';
                this.sCode = '';
                this.linkType = 'no'
                this.boardId = '';
                this.mname = ''
                this.customizeId = '';
                this.muse = 'yes';

            },
            addSubMenu(index) {
                for (let item of this.siteMenuList) {
                    item.active = '';
                    if (item.subList) {
                        for (let subItem of item.subList) {
                            subItem.active = '';
                        }
                    }
                }

                let item = this.siteMenuList[index];
                item.active = 'active';
                if (!item.subList) item.subList = [];
                this.insert = true;
                this.update = false;
                this.subIndex = index;

                const codeDate = new Date();
                const code = 'm' + codeDate.getTime();
                this.fCode = item.code;
                this.sCode = code;
                this.linkType = 'no'
                this.boardId = '';
                this.customizeId = '';
                this.mname = ''
                this.muse = 'yes';
            },

            deletesiteMenu(index) {
                if (this.siteMenuList[index].subList && this.siteMenuList[index].subList.length > 0) {
                    const msgParams = {
                        message: '하위 메뉴가 존재하므로 삭제할수 없습니다',
                    }
                    this.$eventBus.$emit('modalOpen', msgParams);
                    return;
                }
                this.deleteConfirm('main', index);

            },
            deleteSubsiteMenu(index, subIndex) {
                this.deleteConfirm('sub', index, subIndex);

            },
            deleteConfirm(type, index, subIndex) {
                const confirm = {
                    type: 'confirm',
                    message: '선택된 메뉴을 삭제하시겠습니까?',
                    doAction: () => {

                        this.deleteAction(type, index, subIndex);
                    }
                }
                this.$eventBus.$emit('modalOpen', confirm);
            },
            deleteAction(type, index, subIndex) {
                if (type == 'main') {
                    this.menuList.splice(index, 1);
                } else {
                    this.menuList[index].subList.splice(subIndex, 1);
                }
                let frm = {menu: JSON.stringify(this.menuList)};
                if (this.serverFlag) return;
                this.serverFlag = true;
                this.$eventBus.$emit('overlay', 'open');
                apiService.post(API_ADMIN_UPDATE_SETTING_MENU, frm, (res) => {
                    this.serverFlag = false;
                    this.$eventBus.$emit('overlay', 'hide');
                    if (res.status == 'success') {
                        this.fCode = '';
                        this.sCode = '';
                        this.insert = false;
                        this.update = false;
                        this.customizeId = '';
                        this.boardId = '';
                        let params = {
                            message: '메뉴가 삭제 되었습니다'
                        }
                        this.$eventBus.$emit('modalOpen', params);
                        this.getData();

                    }
                })

            },
            sarchArticle() {
                if(this.serverFlag)return;
                if(!this.boardId)return;

                if(!this.articleSubject) {
                    let params = {
                        message: '검색어를 입력하세요'
                    }
                    this.$eventBus.$emit('modalOpen', params);
                }
                this.serverFlag = true;
                let boardIds = [this.boardId];
                let frm = {
                    orderByField: 'createAt',
                    orderBySort: 'desc',
                    limit: '100',
                    page:'1',
                    keywordCmd: 'subject',
                    keyword: this.articleSubject,
                    dateCmd: '',
                    stdate: '',
                    endate: '',
                    boardIds: boardIds,
                    categorys:[],
                    reply:'',
                    open:'',
                    notice:'',

                }
                this.$eventBus.$emit('overlay', 'open');
                apiService.post(API_ADMIN_GET_BOARD_ARTICLE_LIST, frm, (res) => {
                    this.serverFlag = false;
                    this.$eventBus.$emit('overlay', 'hide');
                    this.articleList = [];
                    this.articleId = '';
                    if(res.articleData) {
                        for(const item of res.articleData.list) {
                            this.articleList.push({text:item.subject+' ['+item.userName+']['+item.createAt.substr(0,10)+']',value:item.id})
                        }
                    }

                })
            },
            validate() {
                this.$v.$touch();
                //    if (this.$v.$anyError) return;

                for (const key in this.$v.$params) {
                    if (key == 'mname' && this.$v.mname.$anyError) {
                        return;
                    }
                    if (this.linkType == 'board') {
                        if (key == 'boardId' && this.$v.boardId.$anyError) {
                            return;
                        }
                        if(this.boardType== 'view') {
                            if(!this.articleId) {
                                let msgParams = {
                                    message: '게시글을 선택하세요',
                                }
                                this.$eventBus.$emit('modalOpen', msgParams);
                                return;
                            }
                            const selectedArticle = this.articleList.filter(item=>item.value == this.articleId);
                            this.articleText = selectedArticle[0].text;

                        }

                    } else if (this.linkType == 'customize') {
                        if (key == 'customizeId' && this.$v.customizeId.$anyError) {
                            return;
                        }
                    } else if (this.linkType == 'url') {
                        if(!this.urlWinType) {
                            this.urlWinType = 'now';
                        }
                        if(!this.urlLink) {
                            let msgParams = {
                                message: 'URL을 입력하세요',
                            }
                            this.$eventBus.$emit('modalOpen', msgParams);
                            return;
                        }

                    }
                }


                if (this.update && this.fCode) {
                    let targetData = {};
                    let index = 0;
                    let targetFindex = '';
                    let targetSindex = '';
                    for (const item of this.menuList) {

                        if (this.fCode == item.code) {
                            if (!this.sCode) {
                                targetData = item;
                                targetFindex = index;
                            } else {
                                targetFindex = index;
                                let subIndex = 0;
                                for (const subItem of item.subList) {
                                    if (this.sCode == subItem.code) {
                                        targetData = subItem;
                                        targetSindex = subIndex;
                                        break;
                                    }
                                    subIndex++;
                                }
                            }
                            break;
                        }
                        index++;
                    }

                    let data = {
                        mname: this.mname,
                        code: targetData.code,
                        linkType: this.linkType,
                        boardId: this.boardId,
                        customizeId: this.customizeId,
                        muse: this.muse,
                        boardType:this.boardType,
                        articleId:this.articleId,
                        articleText:this.articleText,
                        urlWinType:this.urlWinType,
                        urlLink:this.urlLink,

                    };
                    if (!this.sCode) {
                        data.subList = targetData.subList;
                        this.$set(this.menuList, targetFindex, data);
                    } else {
                        this.$set(this.menuList[targetFindex].subList, targetSindex, data);
                    }
                } else {
                    const code = (this.sCode) ? this.sCode : this.fCode;
                    let data = {
                        mname: this.mname,
                        code: code,
                        linkType: this.linkType,
                        boardId: this.boardId,
                        customizeId: this.customizeId,
                        muse: this.muse,
                        boardType:this.boardType,
                        articleId:this.articleId,
                        articleText:this.articleText,
                        urlWinType:this.urlWinType,
                        urlLink:this.urlLink
                    };
                    if (this.sCode) {
                        let item = this.menuList[this.subIndex];
                        if (!item.subList) item.subList = [];
                        item.subList.push(data)
                    } else {
                        this.menuList.push(data)
                    }


                }

                let frm = {menu: JSON.stringify(this.menuList)};
                if (this.serverFlag) return;
                this.serverFlag = true;
                this.$eventBus.$emit('overlay', 'open');
                apiService.post(API_ADMIN_UPDATE_SETTING_MENU, frm, (res) => {
                    this.serverFlag = false;
                    this.$eventBus.$emit('overlay', 'hide');
                    if (res.status == 'success') {
                        let msg = '저장'
                        if (this.update) {
                            msg = '수정'
                        }
                        let params = {
                            message: '메뉴정보가 ' + msg + ' 되었습니다'
                        }
                        this.$eventBus.$emit('modalOpen', params);

                        this.fCode = '';
                        this.sCode = '';
                        this.insert = false;
                        this.update = false;
                        this.customizeId = '';
                        this.boardId = '';
                        this.articleList = [];
                        this.articleId = '';
                        this.boardType = 'list';
                        this.articleText = '';
                        this.urlWinType = 'now';
                        this.urlLink = '';

                        this.getData();
                    }
                })

            },
            upDownsiteMenu(cmd) {
                if (!this.sequenceParams.code) {
                    return;
                }
                if (!this.sequenceResultSet(cmd)) return;
                if (this.serverFlag) return;
                this.serverFlag = true;

                let frm = {menu: JSON.stringify(this.siteMenuList)};
                apiService.post(API_ADMIN_UPDATE_SETTING_MENU, frm, (res) => {
                    this.serverFlag = false;
                });
            },
            sequenceResultSet(cmd) {
                let index;
                let item;
                let targetIndex;
                let targetItem;
                let pareintIndex;

                switch (this.sequenceParams.type) {
                    case 'main':
                        index = parseInt(this.sequenceParams.index);
                        item = this.siteMenuList[index];
                        if (cmd == 'up') {
                            if (index < 1) return false;
                            targetIndex = index - 1;

                        } else {
                            if (this.siteMenuList.length <= (index + 1)) return false;
                            targetIndex = index + 1;
                        }
                        targetItem = this.siteMenuList[targetIndex];
                        this.siteMenuList.splice(index, 1, targetItem)
                        this.siteMenuList.splice(targetIndex, 1, item)
                        this.sequenceParams.index = targetIndex;

                        break;

                    case 'sub':

                        pareintIndex = parseInt(this.sequenceParams.index);
                        index = parseInt(this.sequenceParams.subIndex);

                        item = this.siteMenuList[pareintIndex].subList[index];

                        if (cmd == 'up') {
                            if (index < 1) return false;
                            targetIndex = index - 1;
                        } else {
                            if (this.siteMenuList[pareintIndex].subList.length <= (index + 1)) return false;
                            targetIndex = index + 1;
                        }
                        targetItem = this.siteMenuList[pareintIndex].subList[targetIndex];
                        this.siteMenuList[pareintIndex].subList.splice(index, 1, targetItem)
                        this.siteMenuList[pareintIndex].subList.splice(targetIndex, 1, item)
                        this.sequenceParams.subIndex = targetIndex;

                        break;

                }
                return true;

            }


        }

    }
</script>

